body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar{background: #333; color:red;}
.todo-list{
	background: #fff;
	margin:auto;
	width: 50%;
	display: flex;
	flex-direction: column;
	align-items: center;
	border: 1px solid #efefef;
	padding: 30px;
}
.todo-item, .card-comp{
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 30px 20px 0;
	width: 70%;
	border-bottom: 1px solid #cecece;
	font-family: Roboto, sans-serif;
	font-weight: 100;
	font-size: 15px;
	color: #333;
}
input[type=checkbox]{
	margin-right: 10px;
}

.meme{
	position: relative;
}

.meme h1{
	position: absolute;
    top: 0;
    z-index: 9;
    left: 35px;
    font-size: 40px;
    background: #fff
}

.meme h2{
	position: absolute;
    top: 200px;
    z-index: 9;
    left: 35px;
    font-size: 40px;
    background: #fff
}

.meme h1::-moz-selection { background: yellow; }

.meme h1::selection { background: yellow; }
.meme h2::-moz-selection { background: yellow; }
.meme h2::selection { background: yellow; }
